import { useEffect, useRef, useState } from 'react';
import './App.css';
import { sendMsgToOpenAI } from './openai';

function App() {
  const msgEnd=useRef(null)

  const [input, setInput] = useState("")
  const [messages, setMessages] = useState([
    {
      text: "hi im ChatGpt, a stte-of-the-art language model developed by OpenAI.I'm designed to understand and generate human-like text based on the iput I recieve .You can ask me questions ,seek information, or even request assistance with various tasks .Just let me know how can I help you !  ",
      isBot: true,
    }
  ])
  useEffect(()=>{
msgEnd.current.scrollIntoView()
  },[messages])


  const handleSend = async () => {
    const text= input ;
    setInput('')
    setMessages([
      ...messages,
      {text ,isBot:false}
    ])
    const res = await sendMsgToOpenAI(text)
    // console.log(res);
    setMessages([...messages,
    { text, isBot: false },
    { text: res, isBot: true }
    ])

  }


  const handleEnter = async(e)=>{
    if(e.key === 'Enter') await handleSend()
  }


const handleQuery=async(e)=>{

  const text= e.target.value ;
  setMessages([
    ...messages,
    {text ,isBot:false}
  ])
  const res = await sendMsgToOpenAI(text)
  // console.log(res);
  setMessages([...messages,
  { text, isBot: false },
  { text: res, isBot: true }
  ])

}
  return (
    <div className="App">
      <div className="sideBar">
        <div className="upperSide">
          <div className="uppserSideTop">
            <img src="https://i.postimg.cc/MKR69trN/1681039084chatgpt-icon.webp" style={{ width: "50px", height: "50px" }} alt="Logo" className="logo" /> <h4 className="brand">ChatGpt</h4><br />
            <button className="midBtn" onClick={()=>{window.location.reload()}}><img src="https://i.postimg.cc/q7fQpB99/R-1-removebg-preview.png" alt="new chat" className="addBtn" />New Chat</button>
            <div className="upperSideBottom">

              <button className="query" onClick={handleQuery} value={"What is Programming ?"}><img src="https://i.postimg.cc/C1Cwc1gY/vector-message-icon-removebg-preview.png"
                alt="Query" />What is Programming ?</button>

              <button className="query"  onClick={handleQuery} value={"How to use an Api ?"}><img src="https://i.postimg.cc/C1Cwc1gY/vector-message-icon-removebg-preview.png"

                alt="Query" />How to use an Api ?</button>

            </div>
          </div>

        </div>
        <div className="lowerSide">
          <div className="listItems"><img src="https://i.postimg.cc/9QdNgq2c/vector-home-icon-symbol-sign-removebg-preview.png" alt="Home" style={{ width: "20px", height: "20px" }}
            className="listItemsImg" />Home</div>
          <div className="listItems"><img src="https://i.postimg.cc/63n0qkQt/OIP-10-removebg-preview-1.png" alt="Saved" className="listItemsImg" style={{ width: "20px", height: "20px" }} />Saved</div>
          <div className="listItems"><img src="https://i.postimg.cc/J7vWjZJJ/6106296.png" alt="Upgrade" className="listItemsImg" style={{ width: "20px", height: "20px" }} />Upgrade to Pro</div>

        </div>

      </div>
      <div className="main">
        <div className="chats">


          {messages.map((message, i) => 
            <div key={i} className={message.isBot ? "chat bot" : "chat"}>
              <img className='chatimg' src={message.isBot ? "https://i.postimg.cc/PJ3sVvn6/openai-chatgpt-logo-icon-free-png-1.webp" : "https://i.postimg.cc/qM4WyLyp/OIP.jpg"} style={{ width: "60px", height: "60px" }} alt="" /><p className="txt">{message.text} </p>
            </div>

          )}
          <div ref={msgEnd}/>


        </div>
        <div className="chatFooter">
          <div className="inp">
            <input type="text" value={input} onKeyDown={handleEnter} onChange={(e) => setInput(e.target.value)} placeholder='send a message' /> <button onClick={handleSend} className="send" ><img src="https://i.postimg.cc/X7YyrRH3/3682321.png" className='sendimg' style={{ width: "20px", height: "20px" }} alt="send" /></button>
          </div>
        </div>



      </div>
    </div>
  );
}

export default App;



